export function getDataReady(callback) {
    LocalForage.keys().then(function(keys){
        // console.log(keys.toString());
        var status = true;
        JUDGESTATUS(keys,0,status,function (status) {
            // console.log(status);
            callback(status);
        })
    })
}
export function JUDGESTATUS(keys,num,status,callback){
    if(num<keys.length){
        LocalForage.getItem(keys[num],function (err, value) {
            if(err)
                console.log(err);
            if(value==null||value.length==0)
                status = false;
            // console.log(keys[num]+'-----'+status);
            JUDGESTATUS(keys,num+1,status,callback);
        })
    }else{
        callback(status);
    }
}
