export function getAllConfigs(callback){
    JudgeVersion(function (st,t) {
        if(st){
            var name = 'types';
            if(ipnsType=='DAS'){
                name = 'types_das';
            }
            LocalForage.getItem(name,function (err,value) {
                if (err)
                    console.log(err);
                var datas = [];
                for(var i in value){
                    var data = {};
                    data.id=value[i].id;
                    data.name=value[i].name;
                    data.tags=value[i].tags;
                    data.tagIds = value[i].tagIds==null||value[i].tagIds==undefined?[]:value[i].tagIds;
                    datas.push(data);
                }
                callback(datas);
            })
        }else{
            $.ajax({
                url:'./data/types.json?v='+t,
                dataType:'json',
                error:function(err){
                    if(err)
                        console.log(err);
                    callback([]);
                },
                success:function(types){
                    var datas = [];
                    for(var i in types){
                        var data = {};
                        data.id=types[i].id;
                        data.name=types[i].name;
                        data.tags=types[i].tags;
                        data.tagIds = types[i].tagIds==null||types[i].tagIds==undefined?[]:types[i].tagIds;
                        datas.push(data);
                    }
                    callback(datas);
                }
            })
        }
    })
}
function JudgeVersion(callback) {
    // console.time('judge time');
    var time = new Date().getTime();
    $.ajax({
        url:'./data/version.json?v='+time,
        dataType:'json',
        error:function(err){
            if(err)
                console.log(err);
        },
        success:function(version){
            var time1;
            if(version.updatetime.toString().indexOf(':')>-1){
                time1 = Date.parse(version.updatetime.toString());
            }else {
                time1 = version.updatetime;
            }
            var t = time1;
            LocalForage.getItem('updateDate',function (err,value) {
                var status = false;

                if(err) {
                    console.log(err);
                }else {
                    if(value!=null){
                        var time0;

                        if(value.toString().indexOf(':')>-1){
                            time0 = Date.parse(value);
                        }else{
                            time0 = value;
                        }

                        // console.log(time0);
                        // console.log(time1);
                        // console.log(time1 <= time0);
                        // t = time0;
                        if (time1 <= time0) {
                            status = true;
                            // t = time0;
                        }else {
                            // t = time1;
                        }

                    }
                }
                // console.timeEnd('judge time');
                callback(status,t);
            })
        }
    })
}
