function getMedia(arr,num,data,st,t,callback){
    if(num<arr.length){
        if(st){
            LocalForage.getItem(arr[num],function (err,value) {
                if (err)
                    console.log(err);
                getMedia(arr,num+1,data.concat(value),st,t,callback);
            })
        }else{
            $.ajax({
                url:'./data/medias/'+arr[num]+'.json?v='+t,
                dataType:'json',
                error:function(err){
                    if(err)
                        console.log(err);
                    getMedia(arr,num+1,data.concat([]),st,t,callback);
                },
                success:function(medias){
                    getMedia(arr,num+1,data.concat(medias),st,t,callback);
                }
            })
        }
    }else{
        callback(data);
    }
}
