import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import Routes from './page/route';
import{Init,CESHI,CESHIDATA,Chushihua,getDataReady,NewInit,dbname} from './page/Controller';
import $ from 'jquery';


export var db = null;
var ipfs = null;
var isDBReady = false;

const IPFS = require('ipfs');
const OrbitDB = require('orbit-db');
const ipfsOptions = {
    repo:'./ipfsrepo',
    preload: { enabled: false },
    config: {
        "Addresses": {
            "Swarm": ['/dns4/ws-star.discovery.libp2p.io/tcp/443/wss/p2p-websocket-star'],
        }
    },
    EXPERIMENTAL: {
        pubsub: true
    }
}

ipfs = new IPFS(ipfsOptions);

ipfs.on('error',(e)=>console.log(e));

ipfs.on('ready',async()=>{
    const orbitdb = await OrbitDB.createInstance(ipfs,{directory: './orbitdb'});
    db = await orbitdb.open(dbname);
    await db.load();
    isDBReady = true;
})

NewInit(function () {
    // console.timeEnd('init complete time : ');
})


ReactDOM.render(< Routes />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
